import { Component, OnInit, ViewChild, ChangeDetectorRef,OnDestroy } from '@angular/core';
import SwiperCore ,{Pagination, Autoplay,SwiperOptions,EffectFade, Navigation, Swiper} from 'swiper';
import { SwiperComponent } from 'swiper/angular';
import { Router } from '@angular/router';
import { Geolocation } from '@capacitor/Geolocation';
import * as ELG from 'esri-leaflet-geocoder';
import { AlertController } from '@ionic/angular';
import { StorageService } from '../services/storage.service';
SwiperCore.use([Pagination,Navigation,Autoplay,EffectFade]);

@Component({
  selector: 'app-onboarding',
  templateUrl: './onboarding.page.html',
  styleUrls: ['./onboarding.page.scss'],
})
export class OnboardingPage implements OnInit,OnDestroy {
  @ViewChild('swiper', { static: false }) swiper?: SwiperComponent;
  public background;
  key = 'AAPK895f48d83b5543ab80c5d058d510d5971uhYgfFtvdNCIB-no6TgvFEbUE-H-h6ZfPfs9GOWCpAtsp9cTGA8zFsd2DiPWwPf';
  result: string;
  selected: string;
  latitude: any;
  longitude: any;
  accuracy: any;
  public config: SwiperOptions ={
    allowTouchMove:false,
    keyboard:true,
     pagination:{
      type: 'progressbar'
    },
    effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        autoplay: {
          delay: 100000,
        },
  };
  constructor(private router: Router, private cd: ChangeDetectorRef,
    private alertController: AlertController,private storageService: StorageService
) { }

  ngOnInit() {
    this.background = '#F4F4F4';
  }


  next(){
    this.swiper.swiperRef.slideNext(500);
  }

  async getLocation(){
    const alert = await this.alertController.create({
      header: 'Permisos necesarios',
      message: 'Para poder brindarte una mejor experiencia, necesitamos que nos permitas acceder a tu ubicación',
      buttons: ['OK'],
    });

    await Geolocation.getCurrentPosition({ enableHighAccuracy: true }).then((resp: any ) => {
      console.log(resp);
        this.latitude = resp.coords.latitude;
        this.longitude = resp.coords.longitude;
        this.swiper.swiperRef.slideNext(500);
    }).catch((error) => {
        (async ()=>{
          await alert.present();
        })();
    });
       const ltlng = await this.storageService.setStorage('latlng',[this.latitude,this.longitude]);
      new ELG.reverseGeocode({
        apikey: this.key
      }).latlng([this.latitude,this.longitude]).run((error, result, response)=>{
      });
      }

  async save(){
    if(this.selected!== null){
      await this.storageService.setStorage('selected',this.selected) ;
      await this.storageService.setOnboarding();
      this.router.navigate(['/principal/inicio']);
    }
  }

  select(event: any){
    console.log(event);
    this.selected = event;
  }
  skip(){
    this.swiper.swiperRef.slideNext(500);
  }

  ngOnDestroy(){
    this.swiper.swiperRef.destroy();
  }

  onAfterTransition() {
    if(this.swiper.swiperRef.activeIndex === 2){
      this.background ='url("../../assets/images/kid-teen.svg") no-repeat center center / cover';
    }
  }
}
